*{
  margin: 0;
  padding: 0;
  color: white;
}
.main-body{
  background-image:url("./icons/spiderbg.jpeg");
  height: 100vh;
  background-size: cover;
}
.stitle{
  display: flex;
  justify-content: center;
  font-family: 'Times New Roman', Times, serif;
  padding-top: 10px;
}
.mdrame{
  margin: 30px 100px 100px 100px;
  margin-left: auto;
  margin-right: auto;
  width: 70%;
  height: 80%;
  display: flex;
  background-color: rgba(255, 0, 0, 0.5);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid white;
  box-shadow: 0 0 25px black;
  border-radius: 30px;
}
.left-img{
  width: 40%;
  height: 100%;
  background-image: url("./icons/Spider-man.jpeg");
  background-size: cover;
  border-left: 1px solid white;
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
}
.right-info{
  width: 60%;
  padding: 10px;
  font-size: 20px;
  font-family: Georgia, serif;
  text-align: justify;
  overflow: auto;
  scrollbar-width: 0;
}
.right-info::-webkit-scrollbar{
  display: none;
}
b{
  font-size: 30px;
}